<script>

  import ProductComponent from '@/components/ProductComponent.vue';

  export default {
    data: function () {
        return {
            product: "노티드 슈가 리치 핸드워시",
            clicked: "All",
            itemBox: ""
        };
    },
    mounted() {
        setInterval(this.fadeSlide, 3000);
    },
    components: { ProductComponent },
    methods: {
        fadeSlide: function () {
            var thisImg = document.querySelector(".slideList").lastChild;
            document.querySelector(".slideList").prepend(thisImg);
            var thisName = document.querySelector(".slideList").lastChild;
            this.product = thisName.getAttribute("alt");
        },
        productMenuClick: function (event) {
            this.clicked = event.target.textContent;
        },
        addItem: function(thisItem){
          var target = thisItem.target;
          var itemPopup = document.querySelector(".itemPopup");

          if(target.classList.contains("addColor")){
            itemPopup.style.opacity = 1;

            setTimeout(function () {
              itemPopup.style.opacity = 0;
            }, 1500)
          }

          if(target.classList.contains("fa-heart")){
            this.itemBox = "찜한 상품"
          }else if(target.classList.contains("fa-cart-shopping")){
            this.itemBox = "장바구니"
          }

        }
    }
}
</script>

